export default {
    props: {
        value: Boolean,
        message: String,
        backgroundClass: String,
    },
    data: function() {
        return {
            isVisible: this.value,
        };
    },
    watch: {
        value: function() {
            this.isVisible = this.value;
        },
    }
};
