const customEvents = { close: 'close' };

export default {
    props: {
        modalVisible: Boolean,
    },
    data: function() {
        return {
            deleteConfirmed: Boolean,
        };
    },
    methods: {
        close: function() {
            this.$emit(customEvents.close, { deleteConfirmed: this.deleteConfirmed });
        },
    },
    computed: {
        visible: {
            get: function() {
                return this.modalVisible;
            },
            set: function() {
                this.close();
            },
        },
    },
};
